/*
  Style Sheet for Rasoul Unlimited's Official Digital Hub
  Author: Mohammad Rasoul Sohrabi (Rasoul Unlimited)
  Custom styles and variable definitions
  */

/* Contact form styles */
.contact-card {
  max-width: 460px;
  margin: var(--space-lg) auto;
  padding: var(--space-lg);
  border: 1px solid var(--border-color);
  border-radius: var(--border-radius-lg);
  background-color: var(--card-bg);
  box-shadow: 0 4px 20px var(--shadow-color-medium);
  display: flex;
  flex-direction: column;
  gap: var(--space-md);
  transition: transform var(--transition-duration-medium)
    var(--transition-easing-standard);
}

.contact-card:focus-within {
  transform: translateY(-4px);
}

.contact-card input,
.contact-card textarea {
  width: 100%;
  padding: var(--space-sm) var(--space-md);
  border: 1px solid var(--border-color);
  border-radius: var(--border-radius-md);
  font-size: 1rem;
  background-color: var(--container-bg);
  color: var(--text-color);
}

.contact-card textarea {
  resize: vertical;
  min-height: 120px;
}

.contact-card button {
  align-self: flex-end;
  padding: var(--space-sm) var(--space-lg);
  background-color: var(--accent-color);
  color: #fff;
  border: none;
  border-radius: var(--border-radius-md);
  cursor: pointer;
  transition: background-color var(--transition-duration-short)
      var(--transition-easing-standard),
    transform var(--transition-duration-short) var(--transition-easing-springy);
}

.contact-card button:hover,
.contact-card button:focus-visible {
  background-color: var(--primary-color);
  transform: translateY(-2px);
}

.success-message {
  color: var(--primary-color);
  text-align: center;
  font-weight: 600;
  opacity: 0;
  transform: translateY(-10px);
  transition: opacity 0.4s ease, transform 0.4s ease;
}

#contact-form.submitted .success-message,
#form-success:target {
  opacity: 1;
  transform: translateY(0);
}

.dark-mode .micro-profile {
  opacity: 1;
  color: var(--text-color);
}

/* ==========================
   CSP-safe helpers (no inline styles)
   ========================== */

.faq-controls-inline {
  display: flex;
  justify-content: flex-end;
  gap: 1rem;
  margin-bottom: 1.5rem;
  padding: 0 0.5rem;
}

.faq-control-btn {
  background: none;
  border: none;
  cursor: pointer;
  font-size: 0.9rem;
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
}

.faq-control-btn--primary {
  color: var(--primary-color);
}

.faq-control-btn--muted {
  color: var(--text-muted);
}

.faq-no-results {
  text-align: center;
  padding: 2rem;
  color: var(--text-muted);
  font-weight: 500;
}

.faq-no-results--hidden {
  display: none;
}

.faq-footer-card {
  text-align: center;
  margin-top: 3rem;
  padding: 2rem;
  background: var(--bg-secondary, #f8f9fa);
  border-radius: 16px;
  border: 1px dashed var(--border-color, #e1e4e8);
}

.faq-footer__title {
  margin-bottom: 1.5rem;
  font-weight: 500;
  font-size: 1.1rem;
}

.faq-footer__cta {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.8rem 2rem;
  border-radius: 50px;
}

.dark-mode .micro-profile .u-email {
  color: var(--text-color-link);
}
.dark-mode .micro-profile a.u-url {
  color: var(--text-color-link);
}
/* Specific Dark Mode Adjustments for Contrast */
.dark-mode .fun-fact-toast {
  background-color: var(--card-bg);
  color: var(--text-color);
  border-right: 6px solid var(--primary-color);
}

.dark-mode .fun-fact-toast i {
  color: var(--primary-color);
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
  scroll-padding-top: var(--navbar-height);
  overflow-x: hidden;
}

body[data-lang="en"] {
  font-family: var(--font-body);
  background: linear-gradient(
    135deg,
    var(--background-start-color) 0%,
    var(--background-end-color) 100%
  );
  margin: 0;
  padding: 0;
  color: var(--text-color);
  line-height: 1.7;
  position: relative;
  padding-top: var(--navbar-height);
  -webkit-font-smoothing: antialiased;
  text-rendering: optimizeLegibility;
  min-height: 100vh;
  overflow-x: hidden;
  animation: backgroundGradient 20s ease infinite;
  background-size: 200% 200%;
  direction: ltr;
  transition: background-color var(--transition-duration-medium)
      var(--transition-easing-standard),
    color var(--transition-duration-medium) var(--transition-easing-standard),
    background var(--transition-duration-medium)
      var(--transition-easing-standard);
}
img[data-src],
.lazy-image {
  opacity: 0;
  transform: translateY(10px);
  transition:
    opacity var(--transition-duration-medium)
      var(--transition-easing-smooth),
    transform var(--transition-duration-medium)
      var(--transition-easing-smooth);
}

img[data-src].loaded,
.lazy-image.loaded {
  opacity: 1;
  transform: none;
}

@keyframes backgroundGradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

a.skip-link {
  position: absolute;
  left: -999px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
}
a.skip-link:focus,
a.skip-link:focus-visible {
  left: var(--space-sm);
  top: var(--space-sm);
  width: auto;
  height: auto;
  clip: auto;
  padding: var(--space-xs) var(--space-sm);
  background-color: var(--accent-color);
  color: #fff;
  border-radius: var(--border-radius-sm);
  text-decoration: none;
  z-index: 1000;
}

.card:focus-within,
.skills-list li:focus-within,
.timeline li:focus-within .timeline-content,
.contact-info a:focus-visible,
.connect-links-block ul li a:focus-visible,
.testimonial-item:focus-within,
.empty-testimonials-state:focus-within {
  outline: none;
  box-shadow: 0 0 0 3px var(--accent-color), 0 0 0 6px var(--shadow-color-light);
  z-index: 5;
  position: relative;
}
.card.is-featured:focus-within {
  box-shadow: 0 0 0 3px var(--highlight-color),
    0 0 0 6px var(--shadow-color-medium), 0 8px 25px var(--shadow-color-medium);
}

.card:focus-within > *,
.timeline li:focus-within .timeline-content > *,
.testimonial-item:focus-within > * {
  outline: none;
  box-shadow: none;
}

.container {
  max-width: var(--max-content-width);
  width: 100%;
  margin: var(--space-md) auto;
  padding: 0 var(--space-lg);
}

.content-area {
  background: var(--container-bg);
  padding: var(--space-xl) var(--space-xxl);
  border-radius: var(--border-radius-lg);
  box-shadow: 0 15px 40px var(--shadow-color-strong);
  border: 1px solid var(--border-color);
  transition: background var(--transition-duration-medium)
      var(--transition-easing-standard),
    box-shadow var(--transition-duration-medium)
      var(--transition-easing-standard),
    border-color var(--transition-duration-medium)
      var(--transition-easing-standard);
}

.navbar {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background-color: var(--navbar-bg);
  box-shadow: 0 4px 20px var(--shadow-color-medium);
  padding: var(--space-sm) 0;
  z-index: 1000;
  text-align: center;
  backdrop-filter: blur(12px);
  border-bottom: 1px solid var(--border-color);
  height: var(--navbar-height);
  display: flex;
  align-items: center;
  justify-content: center;
  transition: background-color var(--transition-duration-medium)
      var(--transition-easing-standard),
    box-shadow var(--transition-duration-medium)
      var(--transition-easing-standard),
    border-color var(--transition-duration-medium)
      var(--transition-easing-standard);
}

.navbar nav ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}

.navbar nav a {
  color: var(--secondary-color);
  text-decoration: none;
  margin: 0 var(--space-md);
  font-weight: 500;
  font-size: 1.05rem;
  letter-spacing: 0.3px;
  transition: color var(--transition-duration-short)
      var(--transition-easing-standard),
    transform var(--transition-duration-short) var(--transition-easing-springy);
  position: relative;
  padding-bottom: var(--space-xs);
  display: inline-block;
  outline: none;
}

.navbar nav a:hover,
.navbar nav a:focus-visible {
  color: var(--primary-color);
  transform: translateY(-3px);
}

.navbar nav a::after {
  content: "";
  position: absolute;
  width: 0;
  height: 3px;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  background-color: var(--accent-color);
  transition: width var(--transition-duration-medium)
    var(--transition-easing-standard);
}

.navbar nav a:hover::after,
.navbar nav a:focus-visible::after {
  width: 100%;
}

.navbar nav .active {
  color: var(--primary-color);
}

.navbar nav a[aria-current="page"] {
  color: var(--primary-color);
}

.navbar nav .active::after {
  width: 100%;
}

.navbar nav a[aria-current="page"]::after {
  width: 100%;
}

#hero {
  position: relative;
  padding-top: var(--section-v-padding);
  padding-bottom: var(--space-xxxl);
  margin-bottom: var(--section-margin-bottom);
  text-align: center;
  direction: ltr;
  overflow: hidden;
}

#hero::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: radial-gradient(
      circle at top left,
      var(--accent-color) 0%,
      rgba(255, 255, 255, 0) 30%
    ),
    radial-gradient(
      circle at bottom right,
      var(--highlight-color) 0%,
      rgba(255, 255, 255, 0) 30%
    );
  z-index: 0;
  pointer-events: none;
}

.hero .profile-image {
  width: 220px;
  height: 220px;
  border-radius: var(--border-radius-full);
  margin-bottom: var(--space-lg);
  object-fit: cover;
  border: 8px solid var(--accent-color);
  box-shadow: 0 15px 35px var(--shadow-color-medium);
  transition: transform var(--transition-duration-medium)
      var(--transition-easing-springy),
    box-shadow var(--transition-duration-medium)
      var(--transition-easing-standard),
    border-color var(--transition-duration-medium)
      var(--transition-easing-standard);
  z-index: 1;
  position: relative;
  max-width: 90%;
  height: auto;
  display: block;
  margin-left: auto;
  margin-right: auto;
}
.hero .profile-image:hover {
  transform: scale(1.05) rotate(1deg);
  box-shadow: 0 20px 50px var(--shadow-color-strong);
}

h1 {
  font-family: var(--font-heading);
  font-size: clamp(2.8rem, 6vw, 4.5rem);
  color: var(--heading-color);
  margin-top: 0;
  margin-bottom: var(--space-xs);
  font-weight: 900;
  line-height: 1.1;
  letter-spacing: -1px;
  text-shadow: 1px 1px 3px var(--shadow-color-light);
  word-break: break-word;
}

.hero .tagline {
  font-family: var(--font-heading);
  font-size: clamp(1.2rem, 3vw, 1.6rem);
  letter-spacing: 0.3px;
  font-weight: 700;
  color: var(--tagline-color);
  margin-bottom: var(--space-xxl);
  margin-top: 0;
}

.hero-icon {
  width: 1em;
  height: 1em;
  margin-inline-start: 0.3rem;
  fill: currentColor;
  vertical-align: middle;
}
.hero-security {
  color: var(--accent-color);
  font-size: 2rem;
  margin-bottom: var(--space-xl);
}

section {
  margin-bottom: var(--section-margin-bottom);
  padding-top: var(--section-v-padding);
  position: relative;
  z-index: 1;
  padding-bottom: var(--section-v-padding);
}
section:last-of-type {
  margin-bottom: var(--space-xl);
  padding-bottom: 0;
}

section::after {
  content: "";
  position: absolute;
  bottom: calc(var(--section-v-padding) * -1 - 1px);
  left: 50%;
  transform: translateX(-50%);
  width: 200px;
  height: 3px;
  background: linear-gradient(
    to right,
    rgba(0, 90, 158, 0) 0%,
    var(--primary-color) 50%,
    rgba(0, 90, 158, 0) 100%
  );
  z-index: 0;
  pointer-events: none;
}
section:last-of-type::after {
  display: none;
}

section h2 {
  font-family: var(--font-heading);
  font-size: clamp(1.8rem, 4vw, 2.8rem);
  letter-spacing: 0.5px;
  color: var(--heading-color);
  margin-top: 0;
  margin-bottom: var(--space-xl);
  position: relative;
  text-align: center;
  font-weight: 800;
}

section h2::after {
  content: "";
  display: block;
  width: 100px;
  height: 5px;
  background-color: var(--primary-color);
  margin: var(--space-sm) auto 0;
  border-radius: 2.5px;
}

#about p {
  text-align: justify;
  max-width: var(--max-paragraph-width);
  margin-left: auto;
  margin-right: auto;
  font-size: 1.15rem;
  color: var(--text-color);
  line-height: 1.8;
  direction: ltr;
  text-align: left;
}

/* Generic inline links inside the content (valid selector) */
.content-area a {
  color: var(--text-color-link);
  text-decoration: underline;
  transition: color var(--transition-duration-short) ease,
    text-decoration var(--transition-duration-short) ease;
  font-weight: 500;
}

.content-area a:hover {
  color: var(--primary-color);
  text-decoration: none;
}


.content-area
  a:not(.navbar nav a):not(.card .card-links a):not(.contact-info a):not(
    .connect-links-block ul li a
  ):not(.footer a):hover {
  color: var(--primary-color);
  text-decoration: none;
}

.grid-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: var(--space-lg);
  padding: 0;
  align-items: stretch;
}

.card {
  background-color: var(--card-bg);
  border: 1px solid var(--border-color);
  border-radius: var(--border-radius-lg);
  padding: var(--space-md);
  box-shadow: 0 8px 25px var(--shadow-color-medium);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  transition: transform var(--transition-duration-medium)
      var(--transition-easing-standard),
    box-shadow var(--transition-duration-medium)
      var(--transition-easing-standard),
    background-color var(--transition-duration-medium)
      var(--transition-easing-standard),
    border-color var(--transition-duration-medium)
      var(--transition-easing-standard);
  text-align: left;
  overflow: hidden;
  position: relative;
  z-index: 1;
  outline: none;
}

.policy-container {
  background-color: var(--card-bg);
  border: 1px solid var(--border-color);
  border-radius: var(--border-radius-lg);
  padding: var(--space-lg);
  box-shadow: 0 2px 6px var(--shadow-color-light);
  margin-bottom: var(--space-lg);
}

.policy-card {
  border: 2px solid var(--primary-color);
  transition: box-shadow var(--transition-duration-short)
      var(--transition-easing-standard),
    border-color var(--transition-duration-short)
      var(--transition-easing-standard);
}

.policy-card:hover,
.policy-card:focus-within {
  box-shadow: 0 8px 25px var(--shadow-color-medium);
}

.modern-card {
  background: linear-gradient(
    135deg,
    var(--background-start-color),
    var(--background-end-color)
  );
}

.policy-container h2:first-child {
  margin-top: 0;
}

.card:hover {
  transform: translateY(calc(var(--space-2) * -1));
  box-shadow: 0 18px 50px var(--shadow-color-strong);
  filter: saturate(1.05);
  z-index: 2;
}

.card:active {
  transform: translateY(-2px);
  box-shadow: 0 10px 30px var(--shadow-color-medium);
  filter: brightness(1) saturate(1);
}

.card::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: auto;
  width: 100%;
  height: 4px;
  background-color: var(--accent-color);
  transform: translateY(-100%);
  transition: transform var(--transition-duration-medium)
    var(--transition-easing-standard);
  z-index: 2;
}
.card:hover::before {
  transform: translateY(0);
}

.project-image-placeholder,
.content-image-placeholder {
  width: calc(100% + var(--space-md) * 2);
  margin: calc(-1 * var(--space-md)) calc(-1 * var(--space-md)) var(--space-md)
    calc(-1 * var(--space-md));
  height: 180px;
  background-color: var(--skills-bg);
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  border-bottom: 1px solid var(--border-color);
  border-top-left-radius: var(--border-radius-md);
  border-top-right-radius: var(--border-radius-md);
}

.project-image-placeholder img,
.content-image-placeholder img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform var(--transition-duration-medium)
    var(--transition-easing-standard);
}
.card:hover .project-image-placeholder img,
.card:hover .content-image-placeholder img {
  transform: scale(1.05);
}

.lang-switcher {
  text-align: center;
  margin: 10px 0;
  font-family: var(--font-body);
  font-size: 14px;
  color: var(--text-color-light);
}

.lang-switcher .lang-link {
  text-decoration: none;
  color: var(--primary-color);
  padding: var(--space-1) var(--space-2);
  transition: color var(--transition-duration-short)
    var(--transition-easing-standard);
}

.lang-switcher .lang-link:hover {
  color: var(--accent-color);
  text-decoration: underline;
}

.lang-switcher .active {
  font-weight: bold;
  text-decoration: underline;
  color: var(--lang-switcher-active-color);
}

.card h3 {
  font-family: var(--font-heading);
  margin-top: 0;
  margin-bottom: var(--space-sm);
  color: var(--secondary-color);
  font-size: 1.4rem;
  font-weight: 700;
  line-height: 1.4;
  word-break: break-word;
  direction: ltr;
  text-align: left;
}

.card p {
  margin-bottom: var(--space-md);
  font-size: 1rem;
  color: var(--text-color);
  flex-grow: 1;
  line-height: 1.7;
  direction: ltr;
  text-align: left;
}

.card .card-meta {
  font-size: 0.95rem;
  color: var(--text-color-light);
  margin-bottom: var(--space-sm);
  font-weight: 500;
  display: flex;
  flex-wrap: wrap;
  gap: var(--space-sm);
  direction: ltr;
  text-align: left;
  justify-content: flex-start;
}

.card .card-meta i {
  margin-left: var(--space-xs);
  margin-right: 0;
  color: var(--primary-color);
  width: 1em;
  text-align: center;
}

.dopamine-microcopy {
  font-size: 0.9rem;
  color: var(--primary-color);
  font-weight: 600;
  margin-top: var(--space-sm);
  margin-bottom: var(--space-md);
  display: block;
  text-align: left;
  direction: ltr;
  opacity: 0;
  transform: translateY(10px) scale(0.95);
  transition: opacity var(--transition-duration-medium)
      var(--transition-easing-smooth),
    transform var(--transition-duration-medium)
      var(--transition-easing-smooth);
}

.card:hover .dopamine-microcopy {
  opacity: 1;
  transform: translateY(0) scale(1);
}

.card .card-links {
  margin-top: var(--space-sm);
  padding-top: var(--space-sm);
  border-top: 1px solid var(--border-color);
  display: flex;
  flex-wrap: wrap;
  gap: var(--space-sm);
  align-items: center;
  justify-content: flex-start;
}

.card .card-links a {
  font-size: 1rem;
  white-space: nowrap;
  font-weight: 500;
  display: inline-flex;
  align-items: center;
  padding: var(--space-xs) var(--space-sm);
  border-radius: var(--border-radius-sm);
  transition: background-color var(--transition-duration-short)
      var(--transition-easing-standard),
    color var(--transition-duration-short) var(--transition-easing-standard),
    transform var(--transition-duration-short) var(--transition-easing-springy);
  outline: none;
  flex-direction: row;
  direction: ltr;
  text-align: left;
  color: var(--primary-color);
  text-decoration: none;
  border: 1px solid var(--primary-color);
}
.card .card-links a:hover,
.card .card-links a:focus-visible {
  background-color: var(--accent-color);
  color: var(--card-link-hover-text-color);
  transform: translateY(-2px);
  text-decoration: none;
  box-shadow: 0 2px 5px var(--shadow-color-light);
}
.card .card-links a:hover i,
.card .card-links a:focus-visible i {
  color: white;
}
.card .card-links a i {
  margin-right: var(--space-xs);
  margin-left: 0;
  font-size: 0.9rem;
  color: var(--primary-color);
  transition: color var(--transition-duration-short)
    var(--transition-easing-standard);
}

.card.is-featured {
  border-color: var(--highlight-color);
  box-shadow: 0 8px 25px var(--shadow-color-medium),
    0 0 10px var(--highlight-color);
  transform: translateY(0);
  background-color: var(--card-bg);
}
.card.is-featured::before {
  background-color: var(--highlight-color);
}

.card.is-featured:hover {
  transform: translateY(-10px);
  box-shadow: 0 18px 50px var(--shadow-color-strong),
    0 0 15px var(--highlight-color);
}
.card.is-featured:focus-within {
  box-shadow: 0 0 0 3px var(--highlight-color),
    0 0 0 6px var(--shadow-color-medium), 0 8px 25px var(--shadow-color-medium);
}

.mention-icon-placeholder {
  width: calc(100% + var(--space-md) * 2);
  margin: calc(-1 * var(--space-md)) calc(-1 * var(--space-md)) var(--space-md)
    calc(-1 * var(--space-md));
  height: 100px;
  background-color: var(--skills-bg);
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  border-bottom: 1px solid var(--border-color);
  border-top-left-radius: var(--border-radius-md);
  border-top-right-radius: var(--border-radius-md);
  font-size: 3rem;
  color: var(--primary-color);
}
.mention-icon-placeholder i {
  transition: transform var(--transition-duration-medium)
    var(--transition-easing-springy);
}
.card:hover .mention-icon-placeholder i {
  transform: scale(1.1) rotate(5deg);
}

#skills .skills-list {
  list-style: none;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: var(--space-sm);
}
#skills .skills-list li {
  background-color: var(--skills-bg);
  color: var(--secondary-color);
  padding: var(--space-xs) var(--space-md);
  margin: 0;
  border-radius: 20px;
  font-size: 0.95rem;
  font-weight: 500;
  border: 1px solid var(--border-color);
  transition: background-color var(--transition-duration-short)
      var(--transition-easing-standard),
    transform var(--transition-duration-short) var(--transition-easing-springy),
    box-shadow var(--transition-duration-short)
      var(--transition-easing-standard),
    color var(--transition-duration-short) var(--transition-easing-standard);
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  outline: none;
  will-change: background-color, transform, box-shadow, color;
}
#skills .skills-list li:hover,
#skills .skills-list li:focus-visible {
  background-color: var(--primary-color);
  color: white;
  transform: translateY(-3px);
  box-shadow: 0 3px 8px var(--shadow-color-light);
}
#skills .skills-list li.skill-hover-effect {
  transform: translateY(-3px) scale(1.02);
  box-shadow: 0 6px 12px rgba(var(--primary-rgb), 0.3);
}
#skills .skills-list li:active {
  transform: translateY(0);
  box-shadow: 0 2px 8px var(--shadow-color-light);
  background-color: var(--secondary-color);
  color: var(--cta-text-color);
}
#skills .skills-list li i {
  margin-right: var(--space-xs);
  margin-left: 0;
  color: var(--primary-color);
  width: 1em;
  text-align: center;
  transition: color var(--transition-duration-short)
      var(--transition-easing-standard),
    transform var(--transition-duration-short) var(--transition-easing-springy);
  will-change: color, transform;
}
#skills .skills-list li:hover i,
#skills .skills-list li:focus-visible i {
  color: white;
  transform: scale(1.1);
}

#timeline {
  position: relative;
}
#timeline h2 {
  margin-bottom: var(--space-xxl);
}

.timeline-search-container {
  max-width: 400px;
  margin: var(--space-lg) auto;
  text-align: center;
}
.timeline-search-container input {
  width: 100%;
  padding: var(--space-sm) var(--space-md);
  border: 2px solid var(--search-border);
  border-radius: var(--border-radius-md);
  background-color: var(--container-bg);
  color: var(--text-color);
}
.timeline-search-container input:focus {
  outline: none;
  border-color: var(--primary-color);
  box-shadow: 0 0 0 3px var(--search-focus-shadow);
}

.refresh-button {
  margin-bottom: var(--space-md);
  padding: var(--space-xs) var(--space-md);
  border: 2px solid var(--primary-color);
  background: transparent;
  color: var(--primary-color);
  border-radius: var(--border-radius-md);
  cursor: pointer;
  transition: background-color var(--transition-duration-short)
      var(--transition-easing-standard),
    color var(--transition-duration-short) var(--transition-easing-standard);
}
.refresh-button:hover,
.refresh-button:focus {
  background-color: var(--primary-color);
  color: #fff;
}
.refresh-button.loading {
  opacity: 0.6;
  pointer-events: none;
}
.refresh-button.loading::after {
  content: "";
  width: 1em;
  height: 1em;
  border: 2px solid currentColor;
  border-top-color: transparent;
  border-radius: 50%;
  margin-inline-start: 0.5rem;
  animation: spin 0.6s linear infinite;
}

.timeline {
  position: relative;
  width: 100%;
  padding: var(--space-md) 0;
  list-style: none;
  direction: ltr;
}

.timeline::after {
  content: "";
  position: absolute;
  width: 3px;
  background-color: var(--timeline-line-color);
  top: 0;
  bottom: 0;
  left: 50%;
  margin-left: -1.5px;
}

.timeline li {
  padding: var(--space-lg) 0;
  position: relative;
  background-color: inherit;
  width: 50%;
  outline: none;
  opacity: 0;
  transform: translateY(50px);
  transition: opacity 0.6s ease-out, transform 0.6s ease-out;
  will-change: opacity, transform;
}

.timeline li.timeline-item-visible {
  opacity: 1;
  transform: translateY(0);
}

.timeline li:nth-child(odd) {
  left: 50%;
  padding-left: var(--space-xl);
  padding-right: var(--space-md);
}

.timeline li:nth-child(even) {
  padding-right: var(--space-xl);
  padding-left: var(--space-md);
}

.timeline li::after {
  content: "";
  position: absolute;
  width: var(--space-lg);
  height: var(--space-lg);
  right: -14px;
  left: auto;
  background-color: var(--accent-color);
  border: 4px solid var(--timeline-dot-border-color);
  top: 30px;
  border-radius: var(--border-radius-full);
  z-index: 1;
  transition: background-color var(--transition-duration-short)
      var(--transition-easing-standard),
    border-color var(--transition-duration-short)
      var(--transition-easing-standard),
    transform var(--transition-duration-short) var(--transition-easing-springy);
}

.timeline li:nth-child(even)::after {
  left: -14px;
  right: auto;
}

.timeline li:hover::after,
.timeline li:focus-within::after {
  background-color: var(--primary-color);
  border-color: var(--accent-color);
  transform: scale(1.2);
}

.timeline-content {
  padding: var(--space-md);
  background-color: var(--card-bg);
  position: relative;
  border-radius: var(--border-radius-lg);
  box-shadow: 0 4px 15px var(--shadow-color-light);
  transition: transform var(--transition-duration-short)
      var(--transition-easing-springy),
    box-shadow var(--transition-duration-short)
      var(--transition-easing-standard);
  z-index: 1;
  outline: none;
  direction: ltr;
  text-align: left;
  border: 1px solid var(--border-color);
}
.timeline li:hover .timeline-content,
.timeline li:focus-within .timeline-content {
  transform: translateY(-5px);
  box-shadow: 0 6px 20px var(--shadow-color-medium);
}

.timeline-icon {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
  width: var(--space-xxl);
  height: var(--space-xxl);
  background-color: var(--primary-color);
  color: white;
  border-radius: var(--border-radius-full);
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.5rem;
  z-index: 2;
  border: 3px solid var(--container-bg);
}
.timeline li:nth-child(even) .timeline-icon {
  left: auto;
  right: 50%;
  transform: translateX(50%) translateY(-50%);
}

.timeline-content::before {
  content: " ";
  position: absolute;
  top: var(--space-md);
  border-width: 8px;
  border-style: solid;
  border-color: transparent transparent transparent var(--card-bg);
  right: -16px;
  left: auto;
}

.timeline li:nth-child(even) .timeline-content::before {
  left: -16px;
  right: auto;
  border-color: transparent var(--card-bg) transparent transparent;
}

.timeline-content::after {
  content: " ";
  position: absolute;
  top: var(--space-md);
  border-width: 9px;
  border-style: solid;
  border-color: transparent transparent transparent
    var(--timeline-content-border-color);
  right: -18px;
  left: auto;
  z-index: -1;
}

.timeline li:nth-child(even) .timeline-content::after {
  left: -18px;
  right: auto;
  border-color: transparent var(--timeline-content-border-color) transparent
    transparent;
  z-index: -1;
}

.timeline-content h3 {
  font-family: var(--font-heading);
  margin-top: var(--space-sm);
  margin-bottom: var(--space-xs);
  color: var(--secondary-color);
  font-size: 1.3rem;
  font-weight: 700;
}
.timeline-content p {
  font-size: 1rem;
  margin-bottom: var(--space-xs);
  color: var(--text-color);
  direction: ltr;
  text-align: left;
}
.timeline-content .date {
  font-size: 0.9rem;
  color: var(--text-color-light);
  font-weight: 500;
  margin-bottom: var(--space-sm);
  display: block;
  direction: ltr;
  text-align: left;
}

.testimonial-item {
  background-color: var(--card-bg);
  padding: var(--space-lg);
  border-radius: var(--border-radius-xl);
  box-shadow: 0 8px 25px var(--shadow-color-light);
  font-style: italic;
  color: var(--text-color);
  position: relative;
  transition: all 0.3s ease-in-out;
  outline: none;
  direction: ltr;
  text-align: left;
  border-left: 6px solid var(--accent-color);
  margin-bottom: var(--space-lg);
}

.testimonial-item:hover,
.testimonial-item:focus-within {
  transform: translateY(-10px);
  box-shadow: 0 15px 40px var(--shadow-color-medium);
  border-color: var(--primary-color);
}

.testimonial-item::before {
  content: "\201C";
  font-size: 5em;
  color: var(--primary-color);
  position: absolute;
  top: var(--space-xs);
  right: var(--space-md);
  left: auto;
  opacity: 0.08;
  font-family: "Georgia", serif;
  z-index: 0;
}

.testimonial-item p {
  margin-top: var(--space-md);
  margin-bottom: var(--space-md);
  font-size: 1.15rem;
  line-height: 1.8;
  color: var(--text-color);
  font-style: normal;
  position: relative;
  z-index: 1;
  direction: ltr;
  text-align: left;
}

.testimonial-item .author {
  font-weight: 700;
  font-style: normal;
  color: var(--secondary-color);
  text-align: left;
  display: block;
  margin-top: var(--space-md);
  font-size: 1.1rem;
  direction: ltr;
}

.testimonial-item .author-title {
  font-weight: 400;
  font-size: 1rem;
  color: var(--text-color-light);
  direction: ltr;
  display: block;
  margin-top: var(--space-xxs);
}

.testimonials-section {
  padding: var(--space-lg) 0;
  max-width: 900px;
  margin: 0 auto;
}

.giscus-container {
  border: var(--giscus-border);
  border-radius: 20px;
  padding: var(--space-md);
  margin-top: var(--space-lg);
  background: var(--giscus-bg);
  box-shadow: 0 6px 25px var(--shadow-color-medium);
  transition: background 0.3s ease, box-shadow 0.3s ease, border 0.3s ease;
  overflow: hidden;
}

.giscus-container:hover {
  box-shadow: 0 10px 35px var(--shadow-color-strong);
}

.giscus-container iframe {
  width: 100%;
  min-height: 400px;
  border: none;
  border-radius: 12px;
}

.text-right {
  text-align: left;
}

.ltr {
  direction: ltr;
}
.nowrap {
  white-space: nowrap;
}


.subtle-text {
  color: var(--text-color-light);
  font-size: 0.9rem;
  line-height: 1.5;
}

.space-top-xs {
  margin-top: 0.5rem;
}

#connect {
  text-align: center;
  background: linear-gradient(
    135deg,
    var(--background-end-color) 0%,
    var(--skills-bg) 100%
  );
  padding-top: var(--section-v-padding);
