  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  gap: var(--space-md);
  justify-content: center;
}

.connect-links-block ul li {
  margin: 0;
  font-size: 1.05rem;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: var(--space-sm);
  border: 1px solid var(--border-color);
  border-radius: var(--border-radius-sm);
  transition: background-color var(--transition-duration-short)
      var(--transition-easing-standard),
    transform var(--transition-duration-short) var(--transition-easing-springy),
    box-shadow var(--transition-duration-short)
      var(--transition-easing-standard);
  opacity: 1;
  transform: none;
  box-shadow: 0 2px 8px var(--shadow-color-light);
  outline: none;
}
.connect-links-block ul li:hover,
.connect-links-block ul li:focus-within {
  background-color: var(--container-bg);
  transform: translateY(-3px);
  box-shadow: 0 4px 12px var(--shadow-color-medium);
}

.connect-links-block ul li a {
  color: var(--secondary-color);
  text-decoration: none;
  font-weight: 500;
  transition: color var(--transition-duration-short)
    var(--transition-easing-standard);
  display: flex;
  align-items: center;
  flex-grow: 1;
  justify-content: center;
  outline: none;
  padding: 0;
  direction: ltr;
  text-align: center;
}
.connect-links-block ul li a:hover,
.connect-links-block ul li a:focus-visible {
  color: var(--primary-color);
  text-decoration: none;
}
.connect-links-block ul li i {
  margin-right: var(--space-sm);
  margin-left: 0;
  color: var(--accent-color);
  width: 1em;
  text-align: center;
  transition: color var(--transition-duration-short)
    var(--transition-easing-standard);
}
.connect-links-block ul li a:hover i,
.connect-links-block ul li a:focus-visible i {
  color: var(--primary-color);
}

.footer {
  margin-top: var(--space-md);
  font-size: 0.95rem;
  color: var(--footer-color);
  border-top: 1px solid var(--border-color);
  padding: var(--space-xl) 0;
  text-align: center;
  direction: ltr;
}
.footer p {
  margin-top: var(--space-sm);
  margin-bottom: 0;
  font-size: 0.9rem;
  color: var(--footer-color);
  direction: ltr;
  text-align: center;
}

