.final-exploration-toast {
  background: linear-gradient(
    135deg,
    var(--highlight-color) 0%,
    var(--accent-color) 100%
  );
  color: var(--secondary-color);
  border-left: 6px solid var(--primary-color);
  animation: celebrationPulse 2s infinite ease-in-out;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.5), 0 0 20px var(--highlight-color);
  transform: translateX(-50%) translateY(150%) scale(0.9);
  will-change: transform, box-shadow, background;
}

.final-exploration-toast.show {
  transform: translateX(-50%) translateY(0) scale(1);
