    box-shadow: 0 12px 50px rgba(0, 0, 0, 0.6),
      0 0 30px var(--highlight-color);
  }
  100% {
    transform: translateX(-50%) scale(1);
    box-shadow: 0 10px 40px rgba(0, 0, 0, 0.5),
      0 0 20px var(--highlight-color);
  }
}

@keyframes bounceIn {
  0% {
    transform: scale(0.3);
    opacity: 0;
  }
  50% {
    transform: scale(1.1);
    opacity: 1;
  }
  70% {
